@import url(https://fonts.googleapis.com/css?family=Livvic:400);
* {
  margin: 0;
  padding: 0;
  border: 0;
}

code {
  height: 100vh;
}

h1, p, li, a, label, input, button {
  color: #2f2f2f;
  font-family: 'Livvic', sans-serif;
}

img {
  display: block;
  width: 100%;
  max-width: 100%;
}
.app {
  height: 100vh;
}

.container {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 0 24px;
}

.logo {
  max-width: 400px;
}

.maintenanceText {
  position: absolute;
  text-align: center;
  font-size: 1.5em;
  padding: 1em;
  bottom: 0;
}

@media(max-width: 930px) {
  .maintenanceText {
    font-size: 1.25em;
  }
}

@media(max-width: 400px) {
  .maintenanceText {
    font-size: 1em;
  }
}
